@import "../../mixins/textfield-mixin/textfield-mixin.scss";

.textfield-email__container {
  margin: 0;
  padding: 0; 
  width: 320px;
  height: 67px;
  display: block;
  box-sizing: border-box;
}

.textfield-email__form {
  margin: 0;
  padding: 0; 
  text-align: left;
  height:67px;
  position:relative;
}

.textfield-email__label{
  display: inline-block;
  padding: 0;
  margin: 0;
  padding-bottom: 5px;
  height: 5px;
  line-height: 5px;
}

